	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://danmaq.com/!/thC/thC_SS11.jpg\" alt=\"クリックすると詳細を閲覧できます。\"></p>\n<p>あぁあけおめイラスト描く暇なっしんぐ'Ａ｀ 当日隙があれば自分でスケブでも描こうかな。まぁそんなこんなで逝ってきます。……っと、その前にいくつか告知事項が。</p>\n<h2>東方夢終劇 完全版R1.01パッチのリリース</h2>\n<p><a href=\"/!/thC/\">東方夢終劇 <em>完全版R1.01</em>パッチ</a>をリリースします。パッチ形式となっておりますので、インストールには<em>夢終劇R1.00(C71即売用プレス版)が必要</em>です。なお、C71当日にもパッチをFD及びCD-Rに突っ込んで持って行きますが諸事情で少部数のみとなりますので、当日はインターネット環境をお持ちでない方のみお持ち帰り戴きたいと思います。それと当日以降はサーバが重くなる可能性がありますので今のうちダウンロードした人が勝ち組。</p>\n<h2>FLAN v0.2fのリリース</h2>\n<p>東方弾幕風フレームワークライブラリ『<a href=\"/tag/flan\">Framework of LunAtic Notation</a>』のバージョン0.2fをリリースしました。今回は前バージョンのバグ修正のみです。</p>\n<h2>と言うことで</h2>\n<p>久々の拍手レス。かれこれ4か月分溜まってるんでたぶん今日の更新だけだと全部はたぶん無理。</p>\n<h2>(拍)余計に重くなったのはともかく、光符の弱さはまずすぎると思います。～</h2>\n<p>ご意見ありがとうございます。ただ一つだけ言わせてもらうと長文になる場合掲示板にて投稿して貰った方がありがたいです。まぁ、このおかげでWeb拍手もどきCGIの脆弱性を見つけることが出来たんですが(^^;\n本来当初から自機とシナリオは連動の予定だったのですが、その抜け道をうまく用意できずA1～B1.01まで続いてしまったわけです。光符に関してはC1.02以降で若干調整を施しましたが如何でしょうか？</p>\n<h2>(拍)プラクティスじゃなくても、全ステージを、全機種で挑戦出来るようにして欲しいです。～</h2>\n<p>C2/Finalで3機種以上においてクリアした場合解禁されるようになりました。</p>\n<h2>(拍)夢終劇にめるぽ出して！めるぽ！</h2>\n<p>夢終劇はもう満員なので代わりにここで勘弁。</p>\n<p>ξ・∀・)ﾒﾙﾎﾟ</p>\n<h2>(拍)夢終劇、処理落ちする弾幕STGはやりこみが微妙です。～</h2>\n<p>処理落ち関係の意見はPCスペックを晒して頂けるとありがたいです。あと、難易度に関しては前半ステージなので。</p>\n<h2>(拍)中央線でスーツ着てノートPCで夢終劇やってるリーマンの方を見たんですが…Mcさんですか？</h2>\n<p>そうかもしれませんし、一般プレイヤーかもしれません。自分新宿～東京間しか中央線使ってないんでたぶん後者じゃないかなぁ？</p>\n<h2>(拍)夢終劇、どこがどう軽くなったのかさっぱり分かりません。むしろ重さ倍増？</h2>\n<p>とりあえず、ボス登場前のひっかかりはなくなったかと。</p>\n<h2>(拍)デジタルドカタで職場で寝泊りって相当なブラック企業ですね？豊洲ハンズで寝袋、ってところから推察しただけですが。</h2>\n<p>そーなのかー……いやむしろこれが当たり前だと思ってたんだがマジで。ホワイトカラーなんちゃらも然り。</p>\n<h2>(拍)弾幕の美しさもさることながら、時封城のアレンジで好きになった音楽もあるくらいです。～</h2>\n<p>今回音楽は途中から方針の変更でみょん氏に統一となりましたが、その分個々の質を重視したので期待いただけるとありがたいですよー</p>\n<h2>(拍)一部のキャラの扱いがすごく悪いですね。ラスボスやEXボスをザコ扱いとか…</h2>\n<p>東方キャラはたまたま諸悪がそこにいたからラスボスやEXボスになるので、実力でラスボスやEXボスに成り上がるのではないですよ？</p>\n<p>逆に幻想郷中を震撼させるほどの能力者でも遭遇した理由がストーリーとかけ離れている程、前半ステージの雑魚ボスとなっていくわけです。</p>\n<p>例えば、真夏にもかかわらず全ての水が凍結する異変が起きたと言うストーリーを仮定すると、1面ボスに妹紅、ラスボスにチルノって可能性も十分に考えられるわけです。</p>\n<h2>(拍)(｢<em>'ω'</em>)｢ ｽﾞﾋﾞｽﾞﾊﾞｰ</h2>\n<p>誰だか知らんがここまで来たか(｢<em>'ω'</em>)｢ ｽﾞﾋﾞｽﾞﾊﾞｰ</p>\n<h2>(拍)欲しいのは神主の東方。他人の作った東方もどきじゃない。</h2>\n<p>毎度毎度この手の意見がありますけど、そう言う方は大人しくth10を待って戴くのが懸命な判断だと思います。\nでも彼女らの世界と違い、何に於いても不自由なこの世の中せっかちな方も多いですし。自分とか。</p>\n<h2>(拍)夢終劇C1.02ルナボス戦が簡単すぎです。danmaqさんのクオリティーを見せて！＞＜</h2>\n<p>処理落ちの結果そう見えたのでしょうか。自分のPCだと常時60fpsなので、ルナは越すだけでいっぱいいっぱいだったりorz\nここ数年一気にヘタレシューターになってきたなぁ。超リグルとかマジ無理。</p>\n<h2>(拍)～リプレイ再生時に不都合が見つかりました。輝夜が変なスペカを使ってましたよ？</h2>\n<p>C1.03で修正済みです、ゴメンナサイ。</p>\n<h2>(拍)ルナが難しすぎです。こんなの本当に越せるんですか？</h2>\n<p>簡単すぎと言う意見があると思えば難しすぎと言う意見も。\n調整は攻略より難しい。ちなみに自分は避けるのは苦手。むしろ彼女らの弾幕は体張って受けてこそ漢。言い換えるとまく、ならぬまｚ（ｒｙ</p>\n<h2>(拍)PHがあるんですか？メニューで出てきたんですが。</h2>\n<p>時封城も仕様上はPhantasmを実装可能な設計で組み込んでたりします。ストーリー上の絡みで実装しなかったけど。</p>\n<h2>(拍)スペルプラクティスの登録が一度取得したカードしか出てこないのではプラクティスの意味がないと思うのですが～</h2>\n<p>C2/Finalで一度でも越したスペルは取得可否を問わず登録されるようにしました。</p>\n<h2>(拍)推奨環境は余裕でクリアしてるのですが、～45fps位しか出なくなるのですが。掲示板に書くべきか微妙だったので～</h2>\n<p>C1.02は結構重いと言われていたので丁度弾幕風の搭載された新機能をフル活用して同等の見かけで大きく軽量化してみました。それとこの手の意見でしたら掲示板でも十分ですよー。</p>\n<h2>(拍)まーりさー (≡∇≡)</h2>\n<p>あぁその気持ち分かる、痛いほどよく分かるぜ。……だがすまん、魔理沙は俺の5年後の嫁だ 'Ａ｀*</p>\n<h2>(拍)いつまで東方作り続けるつもりですか？</h2>\n<p>消費税が30％くらいになる頃まで。</p>\n<h2>(拍)スペプラに本編で取得に失敗したスペルしか登録されないようです。</h2>\n<p>上に同じくC2/Finalで修正済みです、ゴメンナサイ。</p>\n<h2>(拍)Celeron 1.6GHz、i845Gで時封城がひどく処理落ちします。紅魔郷は完璧に動くのに何でですか？</h2>\n<p>すみません、完全にスペック不足です。\n本来必須環境にPentiumM 1GHz以降と書くべきでしたね。Pentium 1GHz(暗黙的にIIIですね)とPentiumM 1GHzだとNS-1とN700くらい速さが違うのでorz</p>\n<h2>(拍)tiruno</h2>\n<p>あたいNo.⑨ですコンニチワ</p>\n<h2>(拍)danmaqさんってニートですか？キモイ。</h2>\n<p>就職してます。デジタルドカタしてます。キモイ。一定周期で正弦波よろしくガリ痩せ←→激太りを繰り返してます。キモイ。最近は激太りから落ちてきてやっと平均程度になりました。キモイ。</p>\n<h2>(拍)ティアオイエツォン聞きました！26トラックと言うと4面道中でしょうか？かっこいいですね、流石ヨン様！じゃなかったみょん様！～</h2>\n<p>気持ちはありがたいですがちょん様と間違えるのは如何なものかと 'Ａ｀</p>\n<h2>(拍)また本家と勘違いして買う人がいないよう、「これは二次創作です」と大きく書いて欲しいです。</h2>\n<p>時封城も夢終劇もハッキリ書いてますよ、帯に。おまけにこれ届いたとき既に入稿済みなので残念。</p>\n<h2>(拍)今頃入稿して間に合うとか相当優遇されてるんですね。コネって羨ましいです。</h2>\n<p>ﾋﾝﾄ：国内超速プレス</p>\n<h2>(拍)入稿したんですか。てっきり逃げたと思った。</h2>\n<p>danmaqは退かぬ！媚びぬ！省みぬ！！</p>\n<h2>(拍)エキストラ</h2>\n<p>ラブ\nすっげー入れたくて作ってしまったは良いが没。</p>\n<h2>(拍)danmaqさんみすちーとか本気で描いたら絵上手いのに何で時封城の絵はあんな手抜きなんですか？勿体無いです。</h2>\n<p>重いコンダラ</p>\n<p>ってことで以上かな？何とか時間には間に合ったが出発前から疲れたぜ。んじゃ、そういうことで今度こそ出発。</p>\n<h2>今回の冬コミ(三日目・西あ-65a)の出し物</h2>\n<ul>\n<li>(新刊)東方・縦型STG <a href=\"/!/thC/\">東方夢終劇 ～ Concealed the Conclusion</a> (プレス版CD-ROM:1,000円)</li>\n<li>(既刊)東方・縦型STG <a href=\"/!/thA/\">東方時封城 ～ the Alternative Age</a> (CD-R版特別価格/400円)</li>\n<li>\n<p>(既刊)東方・アレンジCD <a href=\"/!/leila/\">Leila Prismriver</a> (プレス版CD-DA:300円)</p>\n<h2>注意事項いくつか</h2>\n</li>\n</ul>\n<ul>\n<li>新刊、東方夢終劇はプレス版ですが諸事情により在庫がかなり少ないですのでご了承ください。</li>\n<li>ショップ委託は<a href=\"http://www.toranoana.jp/\">とらのあな</a>にて<em>おおよそ1ヶ月遅れ(1月末～2月頭)にて</em>開始となります。</li>\n<li>既刊、<em>東方時封城の再販はCD-R版</em>です。プレス版は頒布終了しております。</li>\n<li><del><a href=\"http://www.toranoana.jp/mailorder/article/04/0000/08/13/040000081343.html\">時封城のプレス版ご希望の方はこちらをご利用ください</a>。(既に品薄のようなのでお早めにどうぞ)</del></li>\n<li>今後時封城のプレス予定はありません。次は時封城v2にてプレス版を製作し頒布予定です。</li>\n<li>なお、時封城v2は来年夏イベントに延期しております。</li>\n<li>当日においては、釣銭はなるべく出ないようにご協力お願いします。</li>\n</ul>","frontmatter":{"cover":null,"date":"2006-12-30T00:00:00+09:00","strDate":"2006/12/29","title":"さて前日だぜ？"}}},"pathContext":{"path":"/ja/blog/2006/12/30-さて前日だぜ？/","langKey":"ja"}}

